html {
  padding: 1rem;
  font-family: "Ubuntu", sans-serif;
}

body {
/*  border: 1px solid #bbb; */
  margin: auto;
  max-width: 60em;
}

h1 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-weight: normal;
}

#main {
  background: #f0f0f0;
  padding: 1rem;
}

#editor {
  height: 25rem;
  width: 100%;
}

#control-panel {
  width: 100%;
  display: table;
  margin-top: 0.5rem;
}

.column {
  display: table-column;
}
.row {
  display: table-row;
}

#command-line-args,
#run,
#stdout {
  font-family: "Ubuntu Mono", monospace;
}

#command-line-args,
#run {
  font-size: 1.2rem;
}

.stderr-msg {
  color: #f57900;
}

.info-msg {
  color: #729fcf;
}

#run-cell {
  display: table-cell;
  padding-left: 0.5rem;
  width: 1px;
}

#run {
  min-width: 7.5rem;
}

#command-line-args {
  box-sizing: border-box;
  display: table-cell;
  width: 100%;
}

#stdout {
  background: black;
  box-sizing: border-box;
  color: white;
  font-size: 1rem;
  margin-bottom: 0;
  margin-top: 0.5rem;
  min-height: 3rem;
  padding: 0.5rem;
  white-space: pre-wrap;
  width: 100%;
}

#footer {
  text-align: right;
  font-size: 0.7rem;
  font-style: italic;
  margin-top: 1rem;
}
